.preview-container {
  height: 100%;
  width: 80%;
  padding-inline: 40px;
  /* > * {
    border: 1px solid black !important;
  } */
  text-align: left;
  position: relative;
}

.Home {
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  display: flex;
  padding: 0 !important;
  width: 100%;
  min-height: 100vh;
  justify-content: center;
}

.home__container {
  width: 80%;

  /* > * {
    border: 1px solid black;
  } */
}

.home__icon:hover {
  transition: all 0.3s ease-in-out;
  transform: scale(2) rotateZ(30deg);
  cursor: pointer !important;
}

.home__title {
  font-weight: bold;
  font-size: 32px;
  margin-left: 12px;
  Letter-spacing: -1px;
  color: blueviolet;
}

.home_motto {
  font-size: 76px !important;
  font-weight: bolder;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  Letter-spacing: -3px;
}

.home_desc {
  font-size: 20px !important;
}

.home__featlister__left {
  justify-content: flex-start !important;
  align-items: flex-start !important;
  padding-right: 0%;
}

.home__featlist {
  font-size: 18px !important;
}

.ShadowView {
  text-align: left;
}

.ShadowBox {
  display: grid;
  grid-template-columns: repeat(3, 33.33%);
  grid-row-gap: 40;
  > div.card {
    margin: 5%;
    height: 300px;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.filter-shadow {
  transition: filter 0.3s ease-out;
  filter: drop-shadow(0px 0px 50px #6445ff);
}

.filter-shadow:hover {
  transition: filter 0.5s ease-in-out;
  cursor: pointer;
  filter: drop-shadow(0px 0px 5px #6445ff);
}

.filter-shadow:active {
  transition: filter 0.1s ease-out;
  filter: none;
}

.HeaderView {
  text-align: left;
}

.evp-popover-view-card {
  display: flex;
  > * {
    margin-right: 12px;
    margin-block: 6px;
  }
}
